@import url('https://fonts.googleapis.com/css2?family=Climate+Crisis&family=Josefin+Sans&family=Rampart+One&family=Roboto:wght@300&display=swap');
*{
    margin:0;
    padding: 0;
   
    box-sizing: border-box;
}
.cards{

    margin: 100px;
    display: inline-block;
}

.card{
   
    margin-left:70px ;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    box-shadow: 5px 10px #888888;
}
.card:hover{
    /* border: 2px solid rgb(20, 20, 20);
    margin-left:30px ;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    box-shadow: 5px 10px #888888; */
    transform: scale(1.1);
}
.card__img{
    height: 350px;
    width: 250px;
    margin: auto;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    object-fit: cover;
    
}
 .but{
    margin: 10px;
    padding: 6px;
    margin-left: 60px;
    border-style:none;
    box-shadow: 6px 5px #c7c6c6;
   
}
.heading{
    text-transform: capitalize;
    display: flex;
    text-align: center;
    justify-content: center;
    background-color: antiquewhite;
    border-bottom: 2px rgb(143, 133, 133) solid;
    font-family: 'Rampart One', cursive;
    padding: 20px;
    font-size: 3rem;
}